html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

.container {
  height: 50rem;
  width: 100%;
  background-color: #000;
  position: relative;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  overflow: hidden;
}
.container__text {
  position: absolute;
  height: 10rem;
  left: 50%;
  transform: translate(-50%, 0);
}
.container__shoe {
  position: absolute;
  height: 14rem;
  bottom: 15rem;
  left: 50%;
  transform: translate(-50%, 0);
}
.container__shoe-2 {
  position: absolute;
  height: 14rem;
  bottom: 3rem;
  left: 20%;
}
.container__shoe-3 {
  position: absolute;
  height: 14rem;
  bottom: 0.5rem;
  left: 50%;
}
.container__shoe-4 {
  position: absolute;
  height: 14rem;
  bottom: 3rem;
  left: 80%;
}
.container__shoe-5 {
  position: absolute;
  height: 14rem;
  bottom: 25rem;
  left: 20%;
}
.container__shoe-6 {
